* {
  box-sizing: border-box;
}

.wrap {
  margin: 50px;
  text-align: center;
}

.select-wrap {
  position: relative;
  display: inline-block;
  text-align: left;
  user-select: none;
}

.select {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 577px;
  height: 50px;
  background-color: white;
}
.select::placeholder {
  color: #a7a7a7;
  font-family: Exo2;
  font-size: 15px;
  font-weight: 400;
  line-height: 26px;
}
.select.opened .select-trigger:after {
  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%);
}
.select.opened .options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}
.select.selected .select-trigger {
  color: #000;
}

.select-trigger {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 577px;
  height: 50px;
  padding: 16px 20px;
  background-color: white;
  border: 1px solid #e2e2e2;
  cursor: pointer;
  color: #a7a7a7;
  font-family: Exo2;
  font-size: 15px;
  font-weight: 400;
}
.select-trigger:after {
  position: absolute;
  display: block;
  content: "";
  width: 10px;
  height: 10px;
  top: 50%;
  right: 25px;
  margin-top: -3px;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  transform: rotate(45deg) translateY(-50%);
  transition: all 0.4s ease-in-out;
  transform-origin: 50% 0;
}

.options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0px;
  right: 0px;
  min-width: 100%;
  padding: 5px 0px;
  margin: 5px 0;
  background: #fff;
  transition: all 0.4s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px);
  border: 1px solid #e2e2e2;
  box-shadow: 5px 8.7px 17px rgba(136, 139, 145, 0.2);
}

.option {
  position: relative;
  display: block;
  padding: 5px 20px;
  color: #444;
  font-family: Exo2;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.option:first-of-type {
  border-radius: 4px 4px 0 0;
}
.option:last-of-type {
  border-bottom: 0;
  border-radius: 0 0 4px 4px;
}
.option:hover {
  color: #666;
}